body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  margin: 0;
  padding: 0; }

ul {
  list-style: none; }

.app {
  max-width: 1200px;
  text-align: center;
  margin: 0 auto; }
  .app__header {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%; }
  .app__menu {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%); }
  .app__title {
    font-size: 25px;
    text-align: center;
    color: #195CCF; }

.add-card {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  left: -9999px;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: .3s opacity;
  z-index: 1000; }
  .add-card__close {
    position: absolute;
    top: 15px;
    right: 15px;
    font-size: 35px;
    color: #fff;
    background: transparent;
    border: none;
    cursor: pointer; }
  .add-card__form {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    padding: 25px;
    background-color: #6FC7F5;
    border-radius: 5px; }
  .add-card__input {
    position: relative;
    font-size: 12px;
    min-width: 250px;
    color: #195CCF;
    background-color: #fff;
    border: none;
    padding: 7px 15px;
    border-radius: 5px;
    margin-bottom: 15px; }
    .add-card__input:last-child {
      margin-bottom: 0; }
  .add-card__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    font-weight: bold;
    width: 100%;
    height: 35px;
    color: #6FC7F5;
    background-color: #fff;
    border: none;
    border-radius: 5px;
    text-transform: uppercase;
    transition: .3s;
    cursor: pointer; }
    .add-card__btn:hover {
      color: #195CCF; }
  .add-card.active {
    left: 0;
    opacity: 1; }

