.mnu {
  position: relative; }
  .mnu__list {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    list-style: none; }
  .mnu__item {
    margin-right: 25px; }
    .mnu__item:last-child {
      margin-right: 0; }
  .mnu__add-btn {
    position: relative;
    width: 40px;
    height: 40px;
    border: 2px solid #6FC7F5;
    border-radius: 50%;
    background-color: transparent;
    cursor: pointer;
    transition: .3s; }
    .mnu__add-btn:before, .mnu__add-btn:after {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      width: 60%;
      height: 2px;
      border-radius: 5px;
      background-color: #6FC7F5;
      transform: translate(-50%, -50%);
      transition: .3s; }
    .mnu__add-btn:after {
      width: 2px;
      height: 60%; }
    .mnu__add-btn:hover {
      border-color: #195CCF; }
      .mnu__add-btn:hover:before, .mnu__add-btn:hover:after {
        background-color: #195CCF; }
  .mnu__sort {
    font-size: 14px;
    color: #6FC7F5;
    padding: 5px 10px;
    border: 1px solid #6FC7F5;
    border-radius: 5px;
    cursor: pointer; }
  .mnu__admin-btn {
    position: relative;
    text-align: center;
    border: none;
    color: #fff;
    background-color: #6FC7F5;
    padding: 7px 15px;
    border-radius: 15px;
    cursor: pointer;
    transition: .3s; }
    .mnu__admin-btn:hover {
      background-color: #195CCF; }

